/*style.scss*/
@import '../_config';
@import '../../MediaFiles/Fonts/fonts';


/*
    style.css
    Wezom wTPL v4.0.0
*/
/*-------------------------------  Defs & Sys  ------------------------------------*/
    body {
        position: relative;
        min-width: 320px;
        font-size: $fontSize;
        line-height: $emSizeText;
        color: $colorBlack;
        background-color: #fff;
        -webkit-font-smoothing: antialiased;
    }
    body, button, input, select, textarea {
        font-family: $fontFamily;
    }
    a {
        color: $colorLink;
    }
    .w_tac { text-align: center; }
    .w_tar { text-align: right; }
    .w_tal { text-align: left; }
    .w_fll { float: left; }
    .w_flr { float: right; }
    .w_cls { font-size: 0; display: table; width: 100%;}
    .w_ovh { overflow: hidden; }
    .w_clear { clear: both;}
    .w_blank { height: 25px; }
    .w_first {margin-top: 0 !important;}
    .w_last {margin-bottom: 0 !important;}
    .w_clearfix, .wSize, .wTxt {
        &:after {
            content: '';
            display: block;
            clear: both;
            height: 0;
        }
    }
    .w_block {
        display: block;
        position: relative;
        width: auto;
        background-color: #f8f8f8;
        padding: 2% 3% 2.5%;
        border: 1px solid #ddd;
        border-radius: 3px;
        margin-bottom: 3.5%;
        &:last-child {
            margin-bottom: 0;
        }
    }
    .svgHolder {
        position: relative;
        & > svg {
            @include HVcenter(100%, 100%);
        }
    }
/*-------------------------------  structure  ------------------------------------*/
    .wWrapper {
        min-height: 100%;
        height: auto !important;
        height: 100%;
        position: relative;
        overflow: hidden;
    }
    .wSize {
        max-width: 1400px;
        padding: 0 10px;
        margin: 0 auto;
        position: relative;
    }
    .wContainer {
        padding: 70px 0 100px 0;
        position: relative;
    }
/*-------------------------------  wHeader  ------------------------------------*/
    .wHeader {
        position: fixed;
        width: 100%;
        top: 0;
        left: 0;
        height: 70px;
        background: #fff;
        z-index: 20;
    }
    .logo {
        width: 180px;
        height: 70px;
        margin-right: 2%;
        cursor: pointer;
    }
    .logo_svg {
        height: 70px;
        width: 70px;
        background: #e42855;
        position: relative;
        float: left;
        line-height: 70px;
        text-align: center;
        svg {
            width: 52px;
            height: 40px;
            display: inline-block;
            vertical-align: middle;
            fill: #fff;
        }
    }
    .logo_text {
        height: 70px;
        line-height: 70px;
        width: 110px;
        position: relative;
        float: left;
        text-align: center;
        img {
            display: inline-block;
            vertical-align: middle;
        }
    }
    .phone {
        height: 70px;
        padding: 0 20px;
        box-sizing: border-box;
        background: #f0f0f0;
        -webkit-transition: all 0.3s ease;
        transition: all 0.3s ease;
        text-decoration: none;
        display: block;
        float: left;
        line-height: 70px;
        &:hover {
            background: #e42855;
            .phone_svg {
                svg {
                    fill: #fff;
                }
            }
            .phone_text {
                color: #fff;
            }
        }
        .phone_svg {
            margin-right: 10px;
            height: 14px;
            width: 14px;
            display: inline-block;
            vertical-align: middle;
            svg {
                width: 100%;
                height: 100%;
                fill: #8d9cad;
                -webkit-transition: all 0.3s ease;
                transition: all 0.3s ease;
                display: block;
            }
        }
        .phone_text {
            display: inline-block;
            vertical-align: middle;
            color: #010101;
            font: 16px/20px 'GothamProBold', Arial, Helvetica, sans-serif;
            -webkit-transition: all 0.3s ease;
            transition: all 0.3s ease;
        }
    }
    .callback {
        padding: 0 20px;
        box-sizing: border-box;
        height: 70px;
        line-height: 70px;
        background: #e42855;
        -webkit-transition: all 0.3s ease;
        transition: all 0.3s ease;
        float: left;
        cursor: pointer;
        .callback_text {
            color: #fff;
            font: 14px/20px 'GothamProBold', Arial, Helvetica, sans-serif;
            display: inline-block;
            vertical-align: middle;
            text-transform: uppercase;
        }
        &:hover {
            background: #000;
        }
        &:active {
            -webkit-transform: translate(0,2px);
            transform: translate(0,2px);
        }
        .callback_svg {
            height: 14px;
            width: 14px;
            display: none;
            svg {
                width: 100%;
                height: 100%;
                fill: #fff;
                -webkit-transition: all 0.3s ease;
                transition: all 0.3s ease;
                display: block;
            }
        }
    }
    .language {
        padding: 0 15px;
        box-sizing: border-box;
        height: 70px;
        line-height: 70px;
        position: relative;
        text-align: center;
        float: left;
        &.show {
            .language_drop {
                visibility: visible;
                height: 75px;
                opacity: 1;
                span {
                    opacity: 1;
                    -webkit-transition: opacity 0.3s 0.2s ease;
                    transition: opacity 0.3s 0.2s ease;
                }
            }
        }
    }
    .language_select--cur {
        display: inline-block;
        vertical-align: middle;
        cursor: pointer;
        svg {
            width: 11px;
            height: 7px;
            display: inline-block;
            vertical-align: middle;
            fill: #8d9cad;
            margin-right: 5px;
        }
        span {
            font: 14px/20px 'GothamPro', Arial, Helvetica, sans-serif;
            color: #8d9cad;
        }
    }
    .language_drop {
        position: absolute;
        top: 100%;
        right: 0;
        width: 100%;
        padding: 10px 20px;
        background: #fff;
        visibility: hidden;
        opacity: 0;
        height: 0;
        box-sizing: border-box;
        -webkit-transition: all 0.3s ease;
        transition: all 0.3s ease;
        span {
            display: block;
            cursor: pointer;
            font: 14px/20px 'GothamPro', Arial, Helvetica, sans-serif;
            margin: 5px 0;
            -webkit-transition: color 0.2s ease;
            transition: color 0.2s ease;
            opacity: 0;
            &:hover,
            &.cur {
                color: #e42855;
            }
            &.cur {
                pointer-events: none;
            }
        }
    }
    .wHeaderCenter {
        height: 70px;
        line-height: 70px;
    }
    .menu {
        display: inline-block;
        vertical-align: middle;
        height: 20px;
        line-height: 20px;
        span {
            cursor: pointer;
            display: inline-block;
            vertical-align: top;
            font: 16px/20px 'GothamPro', Arial, Helvetica, sans-serif;
            color: #040404;
            -webkit-transition: all 0.2s ease;
            transition: all 0.2s ease;
            margin: 0 20px;
            &.cur,
            &:hover {
                color: #e42855;
            }
        }
    }
/*-------------------------------  wFooter  -------------------------------*/
    .wFooter {
        position: relative;
        margin: -100px auto 0;
        min-height: 100px;
        background: #21282e;
        line-height: 100px;
        text-align: center;
        z-index: 2;
    }
    .copywrite {
        display: inline-block;
        vertical-align: middle;
        margin-right: 15px;
        font: 14px/25px 'GothamPro', Arial, Helvetica, sans-serif;
        color: #fff;
        span {
            color: #8d888d;
        }
    }
    .wezom {
        display: inline-block;
        vertical-align: middle;
        margin-right: 15px;
        text-decoration: none;
        line-height: 25px;
        svg {
            width: 34px;
            height: 12px;
            display: inline-block;
            vertical-align: middle;
            fill: #d91d24;
            margin-right: 5px;
        }
        span {
            font: 14px/25px 'GothamPro', Arial, Helvetica, sans-serif;
            color: #8d888d;
            display: inline-block;
            vertical-align: middle;
            -webkit-transition: all 0.2s ease;
            transition: all 0.2s ease;
        }
        &:hover {
            span {
                color: #d91d24;
            }
        }
    }
/*-------------------------------  wContainer  ------------------------------------*/
    /*-------------------------------  pageTitles  ------------------------------------*/
        .pageTitle{
            font-size: 44px;
            line-height: $emSizeHeader;
            padding-bottom: $emSizeHalf;
            margin-bottom: $emSizePart;
            font-family: 'OpenSans', Arial, Helvetica, sans-serif;
        }
        .pageTitle.w_middle,
        .pageTitle.w_small {
            margin-top: $emSizePart;
            border-bottom-width: 0;

        }
        .pageTitle.w_middle {
            font-size: 44px * 0.7;
        }
        .pageTitle.w_small {
            font-size: 44px * 0.5;
        }
        .pageTitle.underlined{
            border-bottom: 1px solid $colorMiddleLight;
        }

    /*-------------------------------  wLeft  ------------------------------------*/
        .wLeft {
            position: relative;
            float: left;
            width: 25%;
            margin-right: 3%;
            margin-bottom: 3%;
        }
    /*-------------------------------  wRight  ------------------------------------*/
        .wRight {
            position: relative;
            float: right;
            width: 25%;
            margin-left: 3%;
            margin-bottom: 3%;
        }
    /*-------------------------------  wMiddle  ------------------------------------*/
        .wLeft + .wMiddle,
        .wRight + .wMiddle {
            overflow: hidden;
        }
        .wMiddle {
            position: relative;
        }
/*-------------------------------  slider  -------------------------------*/
    .slider_block {
        background: #fff;
        padding: 60px 0;
        box-sizing: border-box;
    }
    .slider {
        padding-top: 50.5%;
        position: relative;
        box-sizing: border-box;
        z-index: 2;
        .prev,
        .next {
            position: absolute;
            height: 80px;
            width: 80px;
            top: 50%;
            margin-top: -40px;
            background: #000;
            -webkit-transition: all 0.3s ease;
            transition: all 0.3s ease;
            line-height: 80px;
            text-align: center;
            cursor: pointer;
            z-index: 3;
            svg {
                display: inline-block;
                vertical-align: middle;
                width: 20px;
                height: 40px;
                fill: #757575;
                -webkit-transition: all 0.3s ease;
                transition: all 0.3s ease;
            }
            &:hover {
                background: #e42855;
                svg {
                    fill: #fff;
                }
            }
        }
        .prev {
            left: -40px;
        }
        .next {
            right: -40px;
        }
    }
    .css-slider {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        overflow: hidden;
        li {
            float: left;
            line-height: 0;
        }
    }
    .slider_bg {
        width: 100%;
        padding-top: 21.8%;
        position: absolute;
        top: 70px;
        left: 0;
        .slider_bg_container {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            overflow: hidden;
        }
    }
    .js-slider_pagg {
        bottom: 30px;
        position: absolute;
        z-index: 2;
        left: 50%;
        -webkit-transform: translate(-50%,0);
        transform: translate(-50%,0);
        a {
            height: 10px;
            width: 10px;
            display: inline-block;
            vertical-align: top;
            font-size: 0;
            border-radius: 10px;
            background: #fff;
            -webkit-transition: all 0.3s ease;
            transition: all 0.3s ease;
            margin: 0 5px;
            &.selected {
                background: #e42855;
            }
        }
    }
/*-------------------------------  about  -------------------------------*/
    .title_block {
        margin: 0 auto 50px;
        max-width: 790px;
    }
    .title_small {
        font: 12px/15px 'GothamProBold', Arial, Helvetica, sans-serif;
        color: #e42855;
        text-align: center;
        text-transform: uppercase;
        letter-spacing: 6px;
        margin-bottom: 10px;
        &._white {
            color: #fff;
        }
    }
    .title_big {
        color: #000;
        font: 60px/70px 'GothamProBold', Arial, Helvetica, sans-serif;
        text-align: center;
        margin-bottom: 30px;
        &._white {
            color: #fff;
        }
    }
    .slogan {
        color: #787878;
        font: 20px/30px 'GothamProLight', Arial, Helvetica, sans-serif;
        text-align: center;
        &._white {
            color: #fff;
        }
    }
    .about_title {
        text-align: center;
        color: #000;
        font: 40px/50px 'GothamProLight', Arial, Helvetica, sans-serif;
        margin-bottom: 20px;
    }
    .about {
        .wTxt {
            -webkit-columns: 2;
            columns: 2;
            box-sizing: border-box;
            max-width: 1240px;
            margin: 0 auto 50px;
        }
    }
    .now_block {
        height: 460px;
        width: 100%;
        background: url(../pic/now.jpg) top right no-repeat;
        position: relative;
        &:after {
            position: absolute;
            content: '';
            background: url(../pic/glass.png) 0px 0px no-repeat;
            width: 370px;
            height: 137px;
            right: 150px;
            bottom: -35px;
        }
    }
    .now_block_text {
        max-width: 700px;
        float: right;
        padding: 100px 150px 100px 60px;
        box-sizing: border-box;
        width: 100%;
        .now_title {
            color: #000;
            font: 40px/45px 'GothamProLight', Arial, Helvetica, sans-serif;
            margin-bottom: 30px;
        }
        .now_list {
            -webkit-columns: 2;
            columns: 2;
            list-style: disc;
            list-style-position: outside;
            li {
                font: 16px/24px 'GothamPro', Arial, Helvetica, sans-serif;
                color: #000;
                margin-bottom: 10px;
                padding-left: 15px;
                box-sizing: border-box;
                margin-left: 15px;
            }
        }
        p {
            font: 14px/24px 'GothamPro', Arial, Helvetica, sans-serif;
            color: #000;
            max-height: 72px;
            overflow: hidden;
        }
    }
    .beuseful {
        max-width: 1240px;
        margin: 0 auto;
        padding: 80px 0 70px;
        box-sizing: border-box;
    }
    .beuseful_text_block {
        width: 410px;
        float: left;
        padding-right: 50px;
        box-sizing: border-box;
        .beuseful_title {
            color: #000;
            font: 40px/50px 'GothamProLight', Arial, Helvetica, sans-serif;
            margin-bottom: 20px;
        }
        .beuseful_text {
            color: #787878;
            font: 20px/30px 'GothamProLight', Arial, Helvetica, sans-serif;
            margin-bottom: 20px;
        }
        .beuseful_link {
            color: #e42855;
            font: 20px/20px 'GothamProBold', Arial, Helvetica, sans-serif;
            display: inline-block;
            vertical-align: top;
            text-decoration: underline;
            -webkit-transition: all 0.2s ease;
            transition: all 0.2s ease;
            cursor: pointer;
            &:hover {
                text-decoration: none;
            }
        }
    }
    .beuseful_slider_block {
        width: calc(100% - 410px);
        float: right;
        .beuseful_slider_wrap {
            padding-top: 65%;
            position: relative;
            overflow: hidden;
            .beuseful_slider {
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                position: absolute;
                li {
                    position: relative;
                }
                img {
                    cursor: pointer;
                    width: 100%;
                }
            }
        }
        .beuseful_pagg {
            position: absolute;
            bottom: 20px;
            -webkit-transform: translate(-50%,0);
            transform: translate(-50%,0);
            left: 50%;
            text-align: center;
            z-index: 2;
            width: 170px;
            .js-prev {
                float: left;
                height: 38px;
                width: 19px;
                cursor: pointer;
                margin-right: 25px;
                svg {
                    fill: #c8c8c8;
                    -webkit-transition: all 0.3s ease;
                    transition: all 0.3s ease;
                    width: 100%;
                    height: 100%;
                }
                &:hover {
                    svg {
                        fill: #e42855;
                    }
                }
            }
            .js-next {
                float: right;
                height: 38px;
                width: 19px;
                cursor: pointer;
                margin-left: 25px;
                svg {
                    fill: #c8c8c8;
                    -webkit-transition: all 0.3s ease;
                    transition: all 0.3s ease;
                    width: 100%;
                    height: 100%;
                }
                &:hover {
                    svg {
                        fill: #e42855;
                    }
                }
            }
            .js-beuseful_pagg {
                overflow: hidden;
                height: 38px;
                line-height: 38px;
                width: 80px;
                a {
                    display: inline-block;
                    vertical-align: middle;
                    color: #000;
                    text-decoration: none;
                    font: 16px/24px 'GothamProBold', Arial, Helvetica, sans-serif;
                    margin: 0 5px;
                    -webkit-transition: all 0.2s ease;
                    transition: all 0.2s ease;
                    span {
                        font: 16px/24px 'GothamProBold', Arial, Helvetica, sans-serif;
                        display: block;
                    }
                    &:hover {
                        color: #e42855;
                        span {
                            color: #e42855;
                        }
                    }
                    &.selected {
                        font-size: 24px;
                        color: #e42855;
                        span {
                            font-size: 24px;
                            color: #e42855;
                        }
                    }
                }
            }
        }
    }
    .ourmission {
        max-width: 1400px;
        margin: 0 auto;
        padding: 20px 10px 50px;
        box-sizing: border-box;
        border-top: 1px solid #dcdcdc;
    }
    .ourmission_img {
        width: 50%;
        padding: 0 40px;
        box-sizing: border-box;
    }
    .ourmission_text {
        width: 50%;
        padding: 0 150px 0 40px;
        box-sizing: border-box;
        padding-top: 60px;
    }
    .mission_title {
        color: #000;
        font: 40px/40px 'GothamProLight', Arial, Helvetica, sans-serif;
        margin-bottom: 20px;
    }
    .mission_text {
        -webkit-columns: 1 !important;
        columns: 1 !important;
    }
/*-------------------------------  services  -------------------------------*/
    .services {
        background: #f0f0f0 url(../pic/services.jpg) top center no-repeat;
        padding-top: 90px;
        padding-bottom: 75px;
        box-sizing: border-box;
        .title_block {
            margin-bottom: 60px;
        }
    }
    .services_main {
        background: #fff;
    }
    .services_lister {
        min-height: 440px;
    }
    .services_lister_left {
        background: #e42855;
        width: 50%;
        padding: 70px 0;
        box-sizing: border-box;
        position: relative;
        z-index: 2;
        height: 440px;
        .services_item {
            display: block;
            position: relative;
            cursor: pointer;
            padding: 0 14%;
            box-sizing: border-box;
            width: 100%;
            &:nth-child(2) {
                svg {
                    fill: none;
                }
            }
            & + .services_item {
                margin-top: 45px;
            }
            svg {
                width: 40px;
                height: 35px;
                fill: #fff;
                float: left;
                margin-right: 20px;
            }
            span {
                overflow: hidden;
                color: #fff;
                font: 24px/35px 'GothamProBold', Arial, Helvetica, sans-serif;
                display: inline-block;
                vertical-align: top;
            }
            &.cur,
            &:hover {
                span {
                    text-decoration: underline;
                }
            }
            &:after {
                -webkit-transition: all 0.3s ease;
                transition: all 0.3s ease;
                left: 100%;
                width: 20px;
                top: 50%;
                -webkit-transform: translate(0,-50%);
                transform: translate(0,-50%);
                height: 40px;
                content: '';
                position: absolute;
                z-index: 2;
                -webkit-transition: all 0.3s ease;
                transition: all 0.3s ease;
                opacity: 0;
                background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAoAgMAAACDXzJIAAAADFBMVEXkKFXkKFXkKFXkKFVJvPLnAAAAA3RSTlMAiotBcO63AAAATUlEQVQIWx3JsRGDAAADsR8tozEiI2SIFDTgHHLh4tWnd6f/+dsPDAwMDAwMDAwMDAwMDAwMvP/4y3/9kZycnJycnJycnJycnJycnPwHGrFvZDx6o78AAAAASUVORK5CYII=) 0px 0px no-repeat;
            }
            &.cur:after {
                opacity: 1;
            }
        }
    }
    .services_lister_right {
        width: 50%;
        overflow: hidden;
        height: 100%;
        height: 440px;
        text-align: center;
        position: relative;
        img {
            max-width: none;
            position: absolute;
            top: 50%;
            left: 50%;
            -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
        }
    }
    .services_text {
        padding: 50px 5.7%;
        box-sizing: border-box;
        -webkit-columns: 2;
        columns: 2;
        display: none;
        &.cur {
            display: block;
        }
    }
    .algoritm {
        border-top: 1px solid #dcdcdc;
        border-bottom: 1px solid #dcdcdc;
        box-sizing: border-box;
        padding: 60px 5.7%;
        text-align: center;
        .algoritm_title {
            color: #000;
            font: 40px/50px 'GothamProLight', Arial, Helvetica, sans-serif;
            margin-bottom: 50px;
        }
    }
    .algoritm_item {
        float: left;
        width: 20%;
        text-align: center;
        padding: 0 20px;
        box-sizing: border-box;
        .algoritm_svg {
            display: inline-block;
            vertical-align: top;
            margin-bottom: 20px;
            svg {
                width: 72px;
                height: 72px;
                fill: #e42855;
                stroke: #e42855;
            }
        }
        .algoritm_text_text {
            display: block;
            color: #000;
            font: 14px/24px 'GothamPro', Arial, Helvetica, sans-serif;
            span {
                font-family: 'GothamProBold', Arial, Helvetica, sans-serif;
                display: block;
            }
        }
    }
    .virtual {
        box-sizing: border-box;
        padding: 60px 5.7%;
    }
    .virtual_text {
        max-width: 580px;
        width: 100%;
        position: relative;
        z-index: 2;
        .virtual_title {
            color: #000;
            font: 40px/50px 'GothamProLight', Arial, Helvetica, sans-serif;
            margin-bottom: 50px;
        }
    }
    .virtual_img {
        position: absolute;
        bottom: 0;
        right: 10px;
        line-height: 0;
        max-height: 99%;
        max-width: 45%;
