    }
    .works_item {
        display: inline-block;
        vertical-align: top;
        width: calc(33.333% - 14px);
        position: relative;
        margin-bottom: 20px;
        overflow: hidden;
        line-height: 0;
        .works_item_wrap {
            padding-top: 61%;
            position: relative;
        }
        img {
            max-width: none;
            position: absolute;
            top: 50%;
            left: 50%;
            -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
        }
        &:after {
            top: 0;
            left: 0;
            content: '';
            width: 100%;
            height: 100%;
            position: absolute;
            background: rgba(228,40,85,0.9);
            opacity: 0;
            -webkit-transition: all 0.3s ease;
            transition: all 0.3s ease;
        }
        &:hover {
            .works_opus {
                opacity: 1;
            }
            .works_category {
                -webkit-transform: translate(0,0);
                transform: translate(0,0);
                opacity: 1;
            }
            .works_name {
                -webkit-transform: translate(0,0);
                transform: translate(0,0);
                opacity: 1;
            }
            .works_link {
                -webkit-transform: translate(0,0);
                transform: translate(0,0);
                opacity: 1;
            }
            &:after {
                opacity: 1;
            }
        }
    }
    .works_item + .works_item {
        margin-left: 20px;
    }
    .works_item:first-child,
    .works_item:nth-child(2) {
        width: calc(50% - 10px);
    }
    .works_item:nth-child(3n+3) {
        margin-left: 0;
    }
    .works_opus {
        position: absolute;
        text-align: center;
        left: 0;
        top: 50%;
        width: 100%;
        -webkit-transform: translate(0,-50%);
        transform: translate(0,-50%);
        z-index: 2;
        text-align: center;
        opacity: 0;
        .works_category {
            color: #fff;
            font: 16px/36px 'GothamProBold', Arial, Helvetica, sans-serif;
            text-align: center;
            opacity: 0;
            -webkit-transition: all 0.3s ease;
            transition: all 0.3s ease;
            -webkit-transform: translate(0,20px);
            transform: translate(0,20px);
            width: 100%;
            padding: 0 10px;
            box-sizing: border-box;
            max-height: 36px;
            overflow: hidden;
        }
        .works_name {
            color: #fff;
