            -webkit-transition: color 0.2s ease, -webkit-transform 0.3s 0.4s ease, opacity 0.3s 0.4s ease;
            transition: color 0.2s ease, transform 0.3s 0.4s ease, opacity 0.3s 0.4s ease;
